


















import Vue, { PropType } from 'vue'
import { PlantType } from '@/types/plant'

export default Vue.extend({
  name: 'ModuleType',
  props: {
    type: {
      type: Object as PropType<PlantType>,
    },
  },
  computed: {
    label(): string {
      return this.type?.label || 'No type'
    },
  },
})
